export default {
    props: ['index','name'],
    data () {
        return {
            iinfor:[
                {ccon:'',index:'regisXy'},
                {ccon:'',index:'livePact'},
                {ccon:'',index:'helpCen'}
            ],
            addre:[
                ": 62F226",
                // ": 13238879702"
            ],
            gy:[],
      }
    },
    created(){
        let that = this
        this.http(this.configs.apiUl+'/user/app/aboutUs?'+this.configs.display,'get','',function(res){
            let datas=res.data
            if(datas.c == 0){
                that.gy = datas.d.about_us.slice(0,5)
            }
        })
    }
}



