import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isRegister : false,//
        isseekPwd: false,//
        visitState: '',//
        access_token: '',//
        user_id: '',//ID
        ws_url: '',//socket
        userSign: '',//IM
        isgift: false,//
    },
    mutations: {
        showRegister (state) {
            state.isRegister = true
            state.isseekPwd = false
        },
        showisseekPwd (state) {
            state.isRegister = false
            state.isseekPwd = true
        },
        hidwRegister (state) {
            state.isRegister = false
            state.isseekPwd = false
        }
    }
});


