import vHeader from '../public/header'
import userBanner from '../public/userbanner'
import Formation from '../public/formation'
import vFooter from '../public/footer'
import $ from 'jquery'
import { Message } from 'element-ui'
export default {
    data () {
        return {
            msg:[
                "",""
            ],
            privateMsg:false,
            sys:true,
            radio:'1',
            face: false,
            emoji:{},
            friendlsit: [],
            chatList: [],
            newChatList: [],
            DialogList: [],
            user: {},
            mesContent: '',
            content: '',
            selSess: '',
            sessMap: {},
            chat_room_id: '',
            to_user_id: '',
            page: 1,
            pagesize: 20,
            msgpage: "",
            lodeText: '',
            keyCode: '',
            isload: false,
            height: ''
        }
    },
    components: {
        vHeader,
        userBanner,
        Formation,
        vFooter
    },
    watch: {
        chatList () {
            let that = this
            if(!this.isload){//
                setTimeout(function(){
                    $('#content').scrollTop($('#content')[0].scrollHeight)//
                })
            }else{
                setTimeout(function(){
                    let h = $('#content .mmsg').height()//
                    $('#content').scrollTop(h-that.height)
                    that.height = h//
                })
            }
        },
        DialogList () {
            //  setTimeout(function(){
            //     $('#Dialogcontent').scrollTop($('#Dialogcontent')[0].scrollHeight)
            // })
        },
        newChatList () {
            let chat = this.newChatList
            let friend = this.friendlsit
            for(var i in chat){
                if(chat[i].chat_room_id == this.chat_room_id){//
                    this.chatList.push(chat[i].dialog)
                    //
                     this.http(this.configs.apiUl + "/user/chat/ignoreUnread?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&chat_room_id="+ this.chat_room_id,"get","",function(res) {
                     })
                }else{
                    for(var j in friend){//
                        if(friend[j].chat_room_id == chat[i].chat_room_id){
                           this.friendlsit[j].unread = Number(friend[j].unread)+1
                        }
                    }
                }
            }
        },
        content () {
            if(this.keyCode == 13){
                this.mesContent += '</br>'
            }else{
                this.mesContent = this.content
            }
        }
    },
    methods: {
        lodemore () {//
            let that = this
            this.page = this.page+1
            that.lodeText = '...'
            //
            this.http(this.configs.apiUl + "/user/chat/systemDialog?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&type=system&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    let dialogdata =  datas.d.system_dialog
                    // that.msgpage = that.chatList[0].dialog_id
                    if(dialogdata.length == 0){
                        that.lodeText = ''
                    }else{
                         that.lodeText = ''
                    }
                    for(var i in dialogdata){
                        dialogdata[i].msg = JSON.parse(dialogdata[i].msg);//
                    }
                    that.DialogList.push(...dialogdata)
                }
            })
        },
        meslodemore () {//
            let that = this
            that.lodeText = '...'
            this.isload = true
            this.http(this.configs.apiUl + "/user/chat/dialog?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&chat_room_id="+that.chat_room_id+"&pagesize=30&dialog_id="+this.msgpage,"get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    let user_dialog = datas.d.user_dialog.reverse()//  
                    if(user_dialog.length == 0){
                        that.lodeText = ''
                        return
                    }            
                    if(user_dialog.length < 30){
                        that.lodeText = ''
                    }else{
                        that.lodeText = ''
                    }
                    that.msgpage = user_dialog[0].dialog_id//ID
                    that.chatList.unshift(...user_dialog)//
                }
            })
        },
        isshow () {
            this.face = false
        },
        emojiImg (text) {//
            this.mesContent += text
            this.face = false
        },
        match (text) {//
            /**
            * emoji
            * @param text
            */
            var re = /\[([^\]\[]*)\]/g;
            var matches = text.match(re) || [];
            var emoji = this.emoji
            for (var j = 0, len = matches.length; j < len; ++j) {
                if(emoji[matches[j]]){
                    text = text.replace(matches[j], '<img class="emoji" src="' + emoji[matches[j]].emojiImg + '" />');
                }		
            }
            return text;
        },
        pressbq(){
            if(this.face){
                this.face = false
            }else{
                this.face = true
            }
        },
        sysbtn(label){
            this.sys = true
            this.privateMsg = false
        },
        msgbtn(name, id){//
            let that = this
            that.chat_room_id = name
            that.to_user_id = id
            this.isload = false
            let friend = this.friendlsit
            that.lodeText = ''
            this.http(this.configs.apiUl + "/user/chat/dialog?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&chat_room_id="+name+"&pagesize=30","get","",function(res) {
                var datas = res.data
                if(datas.c == 0){
                    that.chatList = datas.d.user_dialog.reverse()
                    if(that.chatList.length < 30){
                        that.lodeText = ''
                    }
                    setTimeout(function() {
                         that.height = $('#content .mmsg').height()//
                    });
                    that.msgpage = that.chatList[0].dialog_id
                    that.privateMsg = true
                    that.sys = false
                    //
                     for(var j in friend){
                        if(friend[j].chat_room_id == name){
                           that.friendlsit[j].unread = 0
                        }
                    }
                }
            })
        },
        searchProfileByUserId (id) {//
                

        },
        send (e) {//
            let that = this
            var text = $('#area').val()
            this.isload = false
            // console.log(text.replace(/[\r\n]/g, "<br/>"))
             if(text == ""){
                    Message({
                        message: '',
                        type: "error"
                    })
                    return false
                }
                that.http(that.configs.apiUl + "/user/chat/send?access_token="+that.$store.state.access_token+"&"+that.configs.display+"&chat_room_id="+that.chat_room_id+"&content="+text+"&to_user_id="+that.to_user_id,"get","",function(res) {
                    let datas = res.data
                    if(datas.c == 0){
                        that.mesContent = ""
                        that.chatList.push(datas.d.dialog)
                    }
                })  
        },
        userUnread (id) { //

        }
    },
    mounted () {
        //
        let that = this
            $("textarea").on('keydown',function(event) { 
                    if(event.ctrlKey && event.keyCode==13){
                        that.mesContent += '\n'
                        // that.mesContent += '<br/>'
                        return false
                    }
                    if (event.keyCode == "13") {
                        //  return
                        var arry = that.mesConten
                        event.preventDefault();  //
                        that.send()
                    }
                });

    },
    created(){
        let that = this
        //
        this.http("../../../static/json/emoji.json",'get','',function(res){
            var datas = res.data
            that.emoji = datas;
        })
        //
            //newMsgList [Msg]
            function onMsgNotify(newMsgList) {
                //console.warn(newMsgList);
                var sess, newMsg;
                //
                var sessMap = webim.MsgStore.sessMap();

                for (var j in newMsgList) {//
                    newMsg = newMsgList[j];
                    for(var i in newMsg.elems){
                        let con =  newMsg.elems[i].content.text.replace(/&quot;/g,'"')
                        let str = JSON.parse(con);
                        if(str.type == 'send_chat'){
                            that.newChatList.splice(0,that.newChatList.length);// 
                            that.newChatList.push(str.data)
                        }
                    }
                }
            }
        //
        var listeners = {
            "onConnNotify": onConnNotify //,
            ,
             "onMsgNotify": onMsgNotify //(())
        };
        //
        var options = {
            'isAccessFormalEnv': true, //
            'isLogOn': false //,
        }
        //
        var onConnNotify = function(resp) {
            var info;
            switch (resp.ErrorCode) {
                case webim.CONNECTION_STATUS.ON:
                    webim.Log.warn(': ' + resp.ErrorInfo);
                    break;
                case webim.CONNECTION_STATUS.OFF:
                    info = ': ' + resp.ErrorInfo;
                    // alert(info);
                    webim.Log.warn(info);
                    break;
                case webim.CONNECTION_STATUS.RECONNECT:
                    info = ': ' + resp.ErrorInfo;
                    // alert(info);
                    webim.Log.warn(info);
                    break;
                default:
                    webim.Log.error(': =' + resp.ErrorInfo);
                    break;
            }
        };

        //
        this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.user = datas.d
                let loginInfo = {
                    identifier: datas.d.tim.account,
                    userSig: datas.d.tim.sign,
                    sdkAppID: datas.d.tim.app_id,
                    accountType: datas.d.tim.account_type
                }
                    //IM
                    webim.login(
                        loginInfo,
                        listeners,
                        options,
                        function (resp) {//
                            // console.log(resp)
                        },
                        function (err) {
                            // console.log(err)
                            alert(err.ErrorInfo);
                        })
            }
        })
        //
         this.http(this.configs.apiUl + "/user/chat/dialogList?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.friendlsit = datas.d.user_dialog.items
            }
         })
         //
         this.http(this.configs.apiUl + "/user/chat/systemDialog?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&type=system&page="+this.page+"&pagesize="+this.pagesize,"get","",function(res) {
            var datas = res.data
            if(datas.c == 0){
                that.DialogList = datas.d.system_dialog
                let DialogList = that.DialogList
                for(var i in DialogList){
                    that.DialogList[i].msg = JSON.parse(that.DialogList[i].msg);
                }
            }
         })
    }
}



