import { Message } from 'element-ui'
import $ from 'jquery'
export default {
    props: [ 'live', "anchorid", "anchorData", 'gift','user'],
    data () {
        return {
            activeName: 'first',
            activeName1: 'first',
            value2: true,
            ewm: true,
            bq:{},
            expre: false,
            page:1,
            pagesize:10,
            pagetotal:0,
            items: [],
            content: '...',
            isuser: false,
            islike: true,
            sendText:'',
            IM: {},
            selToID: '',
            chatText: [],
            top: 0,
            userProfile: {},
            live_end: false,
            kill_live: false,
            kill_live_meg: '',
            reason: [],
            bbox: false,
            sfBox: false,
            giftid: '', 
            ungiftid: '',
            // user:{},
            istalk: true,
            notices: '',
            player: {},
            isDisable: false
        }
    },
    computed: {
        classic () {            
            if(this.$store.state.isgift){
                return true
            }else{
                return false
            }
        }
    },
    watch: {
        anchorid () {
            let that = this
            //
            this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=day&'+this.configs.display,'get','',function(res){
                var datas = res.data
                if(datas.c == 0){
                    that.items = datas.d.rank.items
                    that.pagetotal = datas.d.rank.total
                    if(that.items.length == 0){
                        that.content = ''
                    }
                }  
            })
        },
        // islike () {//
        //     setTimeout(()=>{
        //         if(this.islike == false){
        //             this.islike = true
        //         }
        //     },1000*5*60)
        // },
        user () {
            if(this.user.prohibit_talk_time != '0'){
                this.istalk = false
            }
        },
        ishow () {
            this.expre = this.ishow
        },   
        live () {
            let that = this
            if (this.live.anchor_live_img == '') {
                this.live.anchor_live_img = '/static/images/lebobg.png' 
            }
            this.player = new TcPlayer('id_video', {
                "m3u8": this.live.anchor_live_play_m3u8, //
                "flv": this.live.anchor_live_play_flv,
                "autoplay" : true,      //iOSsafari
                "coverpic" : this.live.anchor_live_img,
                "width" :  '100%',//
                "height" : '100%',//
                "wording": {
                    2032: "",
                    2048: "m3u8",
                    1002: '',
                    5: ' MSE  Flash '
                },
                'live': true,
                'listener': function(msg){
                     if(msg.type == "ended"){
                        $('#id_video .vcp-error-tips').text('...').show()
                         setTimeout(()=>{
                             that.play()
                         },2000) 
                    }
                }
            }); 
        },
        anchorData () {
            let that = this
            if (this.anchorData.anchor_live_img == '') {
                this.anchorData.anchor_live_img = '/static/images/lebobg.png' 
            }
            if(this.anchorData.anchor_live_play_flv){
                 this.player = new TcPlayer('id_video', {
                    "m3u8": this.anchorData.anchor_live_play_m3u8, //
                    "flv": this.anchorData.anchor_live_play_flv,
                    "autoplay" : true,      //iOSsafari
                    "coverpic" : this.anchorData.anchor_live_img,
                    "width" :  '100%',//
                    "height" : '100%',//
                    "wording": {
                        2032: "",
                        2048: "m3u8",
                        1002: '',
                        5: ' MSE  Flash '
                    },
                    'live': true,
                    'controls': false,
                    'listener': function(msg){
                         if(msg.type == "ended"){
                            $('#id_video .vcp-error-tips').text('...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                    }
                });
                //  this.pushIM()
            }
            //
            that.$nextTick(function () {
                setTimeout(function(){
                    window._bd_share_config = {
                        "common": {
                            "bdSnsKey": {
                                'tsina':'3867123266',
                                'qzone': '827f27400866b0bc8eb36002d0cf9033',
                                'weixin': 'wxd17f298d6be757b7'
                            },
                            'bdText': ''+that.anchorData.user_nickname+''+that.anchorData.user_id+'......',
                            'bdDesc': ''+that.anchorData.user_nickname+''+that.anchorData.user_id+'......',
                            'bdMini': '2',
                            'bdUrl': that.anchorData.share_url,
                            'bdPic': that.live.anchor_live_img || that.anchorData.anchor_live_img
                        },
                        "share": {},                
                    }
                    // document.getElementById('bdshell_js').src =
                    const s = document.createElement('script');
                    s.type = 'text/javascript';
                    s.src =  'http://bdimg.share.baidu.com/static/api/js/share.js?v=89860593.js?cdnversion=' + ~(-new Date() / 36e5);
                    // document.body.appendChild(s);
                    document.getElementsByTagName('head')[0].appendChild(s)
                },3000)
            })
            document.title =  that.anchorData.user_nickname+'-'
        },
        chatText () {
             setTimeout(function(){
                $('#chat').scrollTop($('#chat')[0].scrollHeight)
            })
        }
    },
    methods: {
        go (id) {
            // var id = this.list[k].user_id
            if(id == this.$store.state.user_id){
                window.open('/userHome')
            }else{
                window.open('/userHome?id='+id)
            }
        },
        play () {
            var that = this
            this.player.destroy()//
             if(this.anchorData.anchor_live_play_flv){
                 this.player = new TcPlayer('id_video', {
                    "m3u8": this.anchorData.anchor_live_play_m3u8, //
                    "flv": this.anchorData.anchor_live_play_flv,
                    "autoplay" : true,      //iOSsafari
                    "coverpic" : this.anchorData.anchor_live_img,
                    "width" :  '100%',//
                    "height" : '100%',//
                    "wording": {
                        2032: "",
                        2048: "m3u8",
                        1002: '',
                        5: ' MSE  Flash '
                    },
                    'live': true,
                    'controls': false,
                    'listener': function(msg){
                         if(msg.type == "ended"){
                            $('#id_video .vcp-error-tips').text('...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                    }
                });
                //  this.pushIM()
            }else{
                this.player = new TcPlayer('id_video', {
                    "m3u8": this.live.anchor_live_play_m3u8, //
                    "flv": this.live.anchor_live_play_flv,
                    "autoplay" : true,      //iOSsafari
                    "coverpic" : this.live.anchor_live_img,
                    "width" :  '100%',//
                    "height" : '100%',//
                    "wording": {
                        2032: "",
                        2048: "m3u8",
                        1002: '',
                        5: ' MSE  Flash '
                    },
                    'live': true,
                    'listener': function(msg){
                        if(msg.type == "ended"){
                            $('#id_video .vcp-error-tips').text('...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                    }
                }); 
            }

        },
        //
        shift(num){
            if(Number(num)>=10000){
                return (Number(num)/10000).toFixed(2) + ''
            }
            return num
        },
        sendGift () {//
            let that = this
            if(this.giftid == ""){
                Message({
                    message: '',
                    type: "error"
                })
                return false
            }
            this.http(this.configs.apiUl+"/live/gift/send?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.anchorid+"&gift_number=1&live_gift_id="+this.giftid,'get','',function(res){
                let datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                    that.user.user_coin = datas.d.user.user_coin
                }else{
                    Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
           })
        },
        inquireUser (id) {//
            let that = this
            this.http(this.configs.apiUl+"/user/profile/card?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.anchorid+"&user_id="+id,'get','',function(res){
               let datas = res.data
                if(datas.c == 0){
                    that.userProfile = datas.d
                    that.isuser = true
                }
           })
        },
        colse () {//
            this.isuser = false
        },
        kicking (id) {//
           if(this.$store.state.user_id == id){//
                return
            }
            this.http(this.configs.apiUl+"/live/room/kick?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.anchorid+"&user_id="+id,'get','',function(res){
                 let datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                    Message({
                        message: '',
                        type: 'success'
                    })
                }else{
                     Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        banned (id) {//
            if(this.$store.state.user_id == id){//
                return
            }
            this.http(this.configs.apiUl+"/live/room/prohibitTalk?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.anchorid+"&user_id="+id,'get','',function(res){
                 let datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                    Message({
                        message: '',
                        type: 'success'
                    })
                }else{
                     Message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            }) 
        },
        setAdmin (id) {//
            if(this.$store.state.user_id == id){//
                return
            }
            let that = this
            this.http(this.configs.apiUl+"/live/roomadmin/add?access_token="+this.$store.state.access_token+'&'+this.configs.display+"&user_id="+id,'get','',function(res){
                    let datas = res.data
                    if(datas.c == 10001){
                        that.$store.commit('showRegister')//
                        return;
                    }
                    if(datas.c == 0){
                        Message({
                            message: '',
                            type: 'success'
                        })
                        that.userProfile.is_anchor_admin = 'Y'
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                }) 
            
        },
        delAdmin (id) {//
            let that = this
            this.http(this.configs.apiUl + "/live/roomadmin/delete?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&user_id="+id,"get","",function(res) {
                    let datas = res.data
                    if(datas.c == 10001){
                        that.$store.commit('showRegister')//
                        return;
                    }
                    if(datas.c == 0){
                        Message({
                            message: '',
                            type: 'success'
                        })
                        that.userProfile.is_anchor_admin = 'N'
                    }else{
                        Message({
                            message: datas.m,
                            type: 'error'
                        })
                    }
                }) 
        },
        //
        handleClick(tab, event) {
            if(this.activeName1 == this.gift[0].id){
               this.$store.state.isgift = true
            }else if(this.activeName1 == this.gift[1].id){
                this.$store.state.isgift = true
            }
        },
        //
        close(){
            this.ewm = false;
        },
        //
        hit(){
            if(this.expre){
                this.expre = false
            }else{
                this.expre = true
            }
        },
        like () {//
            let that = this      
                this.http(this.configs.apiUl+"/live/room/like?access_token="+this.$store.state.access_token+'&'+this.configs.display+'&anchor_user_id='+this.anchorid,'get','',function(res){
                    let datas = res.data
                    if(datas.c == 10001){
                        that.$store.commit('showRegister')//
                        return;
                    }
                    if(datas.c == 0){
                       that.islike = false  
                    }                 
                })
        },
        //
        favour(){
            var y = 40;  
            var num = Math.floor(Math.random() * 8 + 1);
            var index=$('.demo').children('img').length;
            var rand = parseInt(Math.random() * y)+12; 
            var left = parseInt(Math.random() * y); 
            var time = parseInt(Math.random() * 500 + 2000); 
            
            $(".demo").append("<img src=''>");
            $('.demo img:eq(' + index + ')').attr('src','/static/images/taoxin'+num+'.png')
            $('.demo img:eq(' + index + ')').css({'bottom':'0','position': 'absolute','left':'25px'})
            $('.demo img:eq(' + index + ')').animate({
                bottom:"280px",
                opacity:"0",
                left: left,
            },time)
            $(".demo img").each(function() {
                var top = $(this).offset().top;
                if(top <= 404){
                    $(this).remove();
                }
            })
        },
        end () {//  
            this.$confirm('', {
                    title: '',
                    confirmButtonText: '',
                    cancelButtonText: '',
                    showClose: false,
                    center: true
                    }).then(() => {
                        let that = this
                        this.http(this.configs.apiUl + "/live/anchor/endLive?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                            let datas = res.data
                            if(datas.c == 0){
                               that.$router.push({path:'/'})
                            }else{
                                    Message({
                                    message: datas.m,
                                    type: 'error'
                                })
                            }
                                
                        })
                    }).catch(() => {
                            //
                    });
        },
        //
        handleClickbang(tab,event){
            let that = this
             that.items = []
             that.page = 1
             that.content = '...'
            if(this.activeName == 'first'){
                //
                this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=day&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                        that.pagetotal = datas.d.rank.total
                        if(that.items.length == 0){
                            that.content = ''
                        }
                    }
                   
                })
            }else if(this.activeName == 'second'){
                //
                 this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                        that.pagetotal = datas.d.rank.total
                        if(that.items.length == 0){
                            that.content = ''
                        }
                    }  
                })
            }else if(this.activeName == 'third'){
                //
                 this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=all&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                        that.pagetotal = datas.d.rank.total
                        if(that.items.length == 0){
                            that.content = ''
                        }
                    }
                })
            }
        },
        //
        handleCurrentChange(val){
            let that = this
            this.page = val
            if(this.activeName == 'first'){
                //
                this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=day&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                    }
                })
            }else if(this.activeName == 'second'){
                //
                this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=week&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                    }
                })
            }else if(this.activeName == 'third'){
                //
                this.http(this.configs.apiUl+'/live/ranking/anchorFans?access_token='+this.$store.state.access_token+'&anchor_user_id='+this.anchorid+'&page='+this.page+'&pagesize='+this.pagesize+'&type=all&'+this.configs.display,'get','',function(res){
                    var datas = res.data
                    if(datas.c == 0){
                        that.items = datas.d.rank.items
                    }
                })
            }
        },
        //
        attention(id){
            var uid;
            if(id){
                uid = id
            }else{
                uid = this.anchorid
            }
            let that = this
            this.http(this.configs.apiUl+"/user/follow/add?access_token="+this.$store.state.access_token+"&anchor_user_id="+ uid +"&user_id="+ uid +"&"+this.configs.display,'get','',function(res){
                var datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 10002){
                    Message({
                        message: '',
                    })
                    return;
                }
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: '!'
                    })
                     if(id){
                        that.userProfile.is_follow = 'Y'
                    }else{
                        that.anchorData.is_follow = true
                    }
                }else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    })
                }
            })
        },
        //
        unattention(id){
            var uid;
            if(id){
                uid = id
            }else{
                uid = this.anchorid
            }
            var that = this
            this.http(this.configs.apiUl+"/user/follow/delete?access_token="+this.$store.state.access_token+"&user_id="+ uid +'&'+this.configs.display,'get','',function(res){
                let datas = res.data
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: '!'
                    })
                    if(id){
                        that.userProfile.is_follow = 'N'
                    }else{
                        that.anchorData.is_follow = false
                    } 
                }else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    })
                }
            })
        },
        //
        barrage(){
            let that = this
            
            if(this.sendText == ""){
                this.$message({
                    message: "",
                    type: 'error'
                })
                return false
            }
            if(this.isDisable){//
                return
            }
            this.isDisable = true
            this.http(this.configs.apiUl+"/live/room/sendBarrage?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&content="+this.sendText+"&anchor_user_id="+this.anchorid,'get','',function(res){
                var datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                    that.sendText = ''
                    that.isDisable = false
                }else{
                    that.$message({
                        message: datas.m,
                        type: 'error'
                    })
                    
                }
            })
        },
        xinshi (text) {//
            	if(this.value2){
                    if(text == "") {
                        return;
                    };
                    var _height = $('.barrage .screen').height(); //
                    this.top += 75;
                    if(this.top >= (_height)) {
                        this.top = 0;
                    }
                    var _left = $('.barrage .screen').width(); //left
                    if(this.top%150){
                        var _lable = $("<div style='left: "+ _left +"px;top: "+ this.top +"px;opacity:1;color: #fff;' class='active1'>" + this.match(text) + "</div>");
                    }else{
                        var _lable = $("<div style='left: "+ _left +"px;top: "+ this.top +"px;opacity:1;color: #fff;' class='active'>" + this.match(text) + "</div>");
                    }
                    
                    $(".mask").append(_lable.show());
                    $(".mask div").show().each(function() {
                        var divleft = $(this).offset().left;
                        if(divleft <= -500){
                            $(this).remove();
                        }
                    })
                }
        },
        sendmessage () {//
            let that = this
            if(this.istalk == false){
                this.$message({
                    message: "",
                    type: 'error'
                })
                return false
            }
            if(this.sendText == ""){
                this.$message({
                    message: "",
                    type: 'error'
                })
                return false
            }
            if(this.isDisable){//
                return
            }
            this.isDisable = true
            this.http(this.configs.apiUl+"/live/room/sendChat?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&content="+this.sendText+"&anchor_user_id="+this.anchorid,'get','',function(res){
                var datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                     that.isDisable = false
                     that.sendText = ''
                }else{
                    this.$message({
                        message: datas.m,
                        type: 'error'
                    })
                }
            })
        },
        //
        report(){
            if(this.bbox){
                this.bbox = false
            }else{
                this.bbox = true
            }
        },
        reasonly(content){//
            let that = this
            this.http(this.configs.apiUl+"/live/report/room?access_token="+this.$store.state.access_token+"&"+this.configs.display+"&content="+content+"&anchor_user_id="+this.anchorid,'get','',function(res){
                var datas = res.data
                if(datas.c == 10001){
                    that.$store.commit('showRegister')//
                    return;
                }
                if(datas.c == 0){
                    that.$message({
                        type: 'success',
                        message: ""
                    })
                    that.bbox = false
                }else{
                    that.$message({
                        type: 'error',
                        message: datas.m
                    })
                }
            })
        },
        emojiImg (text) {//
            if(this.istalk){
                this.sendText += text
                this.expre = false
            }    
        },
        match (text) {//
            /**
            * emoji
            * @param text
            */
            var re = /\[([^\]\[]*)\]/g;
            var matches = text.match(re) || [];
            var emoji = this.bq
            for (var j = 0, len = matches.length; j < len; ++j) {
                if(emoji[matches[j]]){
                    text = text.replace(matches[j], '<img class="emoji" src="' + emoji[matches[j]].emojiImg + '" width="30" heihgt="30" style="margin-top: -3px;"/>');
                }		
            }
            return text;
        },
        applyJoinBigGroup (groupId){//
         let that = this
            var options = {
                'GroupId': groupId //id
            };
            webim.applyJoinBigGroup(
                options,
                function(resp) {
                    //JoinedSuccess:; WaitAdminApproval:
                    if (resp.JoinedStatus && resp.JoinedStatus == 'JoinedSuccess') {
                        webim.Log.info('');
                        that.selToID = groupId;
                        console.log('')
                        that.$emit("goRoom")//
                    } else {
                        //alert('');
                    }
                },
                function(err) {
                    // alert(err.ErrorInfo);
                }
            );
        },
        showMsg (msg) {//
            var isSelfSend, fromAccount, fromAccountNick, sessType, subType;
                // var ul, li, paneDiv, textDiv, nickNameSpan, contentSpan;
                fromAccount = msg.getFromAccount();
                if (!fromAccount) {
                    fromAccount = '';
                }
                fromAccountNick = msg.getFromAccountNick();
                if (!fromAccountNick) {
                    fromAccountNick = '';
                }
                //
                //
                //webim.SESSION_TYPE.GROUP-
                //webim.SESSION_TYPE.C2C-
                sessType = msg.getSession().type();
                //
                //webim.GROUP_MSG_SUB_TYPE
                //webim.C2C_MSG_SUB_TYPE
                subType = msg.getSubType();

                isSelfSend = msg.getIsSend();//
                this.convertMsgtoHtml(msg);

        },
        convertMsgtoHtml (msg) {
                var html = "", elems,elem, type, content;
                elems=msg.getElems();//
                for (var i in elems) {
                    elem = elems[i];
                    type = elem.getType();//
                    content = elem.getContent();//
                    this.convertGroupTipMsgToHtml(content);
                }
        },
        convertGroupTipMsgToHtml (content) {
            if(content.text){
                let con =  content.text.replace(/&quot;/g,'"')
                let str = JSON.parse(con);
                 if(str.type == 'send_barrage'){//
                    this.xinshi(str.data.chat.content)
                }
                if(str.type == 'room_users'){//
                    this.live.anchor_live_onlines = str.data.onlines
                }
                 if(str.type == 'live_end'){//
                    this.live_end = true
                }
                 if(str.type == 'kill_live'){//
                    this.kill_live = true
                    this.kill_live_meg =  str.msg
                }
                if(str.type == 'kick'){//
                    if(str.data.user.user_id == this.user.user_id){//
                            function zeroize( num ) {
                                return (String(num).length == 1 ? '0' : '') + num;
                            }
                            var theTime = parseInt(str.data.user.kick_time);//  
                            var result = theTime+'s';
                            if(str.data.user.kick_time == '-1'){
                                result = ''
                            }
                            this.$confirm('', {
                                title: result+ '',
                                confirmButtonText: '',
                                showClose: false,
                                center: true,
                                showCancelButton: false
                                }).then(() => {
                                    this.$router.push({path:'/'})
                                }).catch(() => {

                            });
                    }
                }
                if(str.type == 'prohibit_talk'){//
                    if(str.data.user.user_id == this.user.user_id){//
                        this.istalk = false
                            var theTime = parseInt(str.data.user.prohibit_talk_time);//  
                            var result = theTime+'s';
                            if(str.data.user.prohibit_talk_time == '-1'){
                                result = ''
                            }
                            this.$confirm(result+ '', {
                                title: '',
                                confirmButtonText: '',
                                showClose: false,
                                center: true,
                                showCancelButton: false
                                }).then(() => {
                                    // this.$router.push({path:'/'})
                                }).catch(() => {

                            });
                    }
                }
                if(str.type == 'send_like'){
                    this.favour()
                }
               if(str.type == 'send_room_chat' || str.type == 'join' || str.type == 'send_gift'|| str.type == 'kick' || str.type == 'prohibit_talk' || str.type == 'send_like'){//
                    if(str.type == 'send_like'){
                        if(str.data.user.is_first == 'N'){
                            return
                        }
                    }
                    this.chatText.push(str)
                }
            }
        },
        //
        share(){
            if(this.sfBox){
                this.sfBox = false
            }else{
                this.sfBox = true
            }
        },
        pushIM () {
            let that = this
            //----------------------------//IM--------------------------------------------//
            //
            var onConnNotify = function (resp) {
                switch (resp.ErrorCode) {
                    case webim.CONNECTION_STATUS.ON:
                        //webim.Log.warn('...');
                        break;
                    case webim.CONNECTION_STATUS.OFF:
                        webim.Log.warn('');
                        break;
                    default:
                        webim.Log.error(',status=' + resp.ErrorCode);
                        break;
                }
            };
            var jsonpCallback = function(resp){
                
            }
            var onBigGroupMsgNotify = function(msgList){
                for (var i = msgList.length - 1; i >= 0; i--) {//
                    var msg = msgList[i];
                    webim.Log.warn('receive a new group msg: ' + msg.getFromAccountNick());
                    //
                    that.showMsg(msg)
                }
            }
            //
            var listeners = {
                "onConnNotify": onConnNotify, //
                "jsonpCallback": jsonpCallback, //IE9()jsonp,pc
                "onBigGroupMsgNotify": onBigGroupMsgNotify, //()
            };
            //
            var options = {
                'isAccessFormalEnv': true, //
                'isLogOn': false //,
            }

            //
            let access_token = localStorage.getItem('access_token')
            if(access_token == '' || access_token == "undefined"){//
                let userSign = localStorage.getItem('userSign')
                if(userSign == 'null' || userSign == 'undefined' || userSign == ''){
                    this.http(this.configs.apiUl + "/account/register/getSign","get","",function(res) {
                        var datas = res.data
                        if(datas.c == 0){
                            // console.log(that.IM)
                            let loginInfo = {
                                identifier: datas.d.account,
                                userSig: datas.d.sign,
                                sdkAppID: datas.d.app_id,
                                appIDAt3rd: datas.d.app_id,
                                accountType: datas.d.account_type,
                            }
                            that.$store.state.getuserSign = JSON.stringify(loginInfo)
                            //IM
                            webim.login(loginInfo, listeners, options,
                                function (identifierNick) {
                                    //identifierNick()
                                    webim.Log.info('webim');
                                    that.applyJoinBigGroup(that.anchorid);//
                                },
                                function (err) {
                                    //alert(err.ErrorInfo);
                                }
                            );
                        }
                    })
                }else{
                   //IM
                    webim.login(JSON.parse(userSign), listeners, options,
                        function (identifierNick) {
                            //identifierNick()
                            webim.Log.info('webim');
                            that.applyJoinBigGroup(that.anchorid);//
                        },
                        function (err) {
                            // alert(err.ErrorInfo);
                        }
                    );     
                }
            }else{//
                //
                this.http(this.configs.apiUl + "/user/profile/index?access_token="+this.$store.state.access_token+"&"+this.configs.display,"get","",function(res) {
                    var datas = res.data
                    if(datas.c == 0){
                        that.IM = datas.d.tim
                        // console.log(that.IM)
                        let loginInfo = {
                            identifier: datas.d.tim.account,
                            userSig: datas.d.tim.sign,
                            sdkAppID: datas.d.tim.app_id,
                            appIDAt3rd: datas.d.tim.app_id,
                            accountType: datas.d.tim.account_type,
                            identifierNick: datas.d.user_nickname,
                            headurl: datas.d.user_avatar
                        }
                        //IM
                        webim.login(loginInfo, listeners, options,
                            function (identifierNick) {
                                //identifierNick()
                                webim.Log.info('webim');
                                that.applyJoinBigGroup(that.anchorid);//
                            },
                            function (err) {
                                // alert(err.ErrorInfo);
                            }
                        );
                    }
                })                
            }
        }
    },
    mounted () {
        
    },
    created(){   
        let that = this
        // this.notices = this.$route.query.notices
        //
        this.http("../../../static/json/emoji.json",'get','',function(res){
            that.bq = res.data
        })
        //----------------------------------------------------------------// 
        //
        this.http(this.configs.apiUl+"/live/report/index?access_token="+this.$store.state.access_token+'&'+this.configs.display,'get','',function(res){
            let datas = res.data
            if(datas.c == 0){
                that.reason = datas.d.report
            } 
        })
        this.pushIM()//IM
        
        //
        var hiddenProperty = 'hidden' in document ? 'hidden' :    
            'webkitHidden' in document ? 'webkitHidden' :    
            'mozHidden' in document ? 'mozHidden' :    
            null;
        var visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
        var onVisibilityChange = function(){
            if (!document[hiddenProperty]) {    
                // document.title='(**)';
                that.player.play()//
            }else{
                // document.title='()  ';
                that.player.pause()//
            }
        }
        document.addEventListener(visibilityChangeEvent, onVisibilityChange);
    }
}



