import $ from 'jquery'
export default {
  props: ['list'],
  data () {
      return {
          sj: true,
          type: '',
          imgs: "",
          id: '',
          player: {}
      }
  },
  methods: {
      pitch (id, k) {
        var that = this
        $('.vcp-player').remove()
        this.type = k
        //  this.imgs = img
        // this.player.destroy()//[v2.2.1+]
        setTimeout(()=>{
            if(this.list[k].anchor_live_log_playback_url){
                this.player = new TcPlayer('id_test_video', {
                    "m3u8": this.list[k].anchor_live_log_playback_url, //
                    "flv": this.list[k].anchor_live_log_playback_url,
                    "autoplay" : true,      //iOSsafari
                    "coverpic" : this.list[k].anchor_live_img,
                    "width" :  '100%',//
                    "height" : '100%',//
                    "wording": {
                        2032: "",
                        2048: "m3u8",
                        1002: '',
                        5: ' MSE  Flash '
                    },
                    'listener': function(msg){
                        if(msg.type == "ended"){
                            $('#id_test_video .vcp-error-tips').text('...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                    }
                    // 'live': true,
                    // 'controls': 'none'
                }); 
            }else{
                this.player = new TcPlayer('id_test_video', {
                    "m3u8": this.list[k].anchor_live_play_m3u8, //
                    "flv": this.list[k].anchor_live_play_flv,
                    "autoplay" : true,      //iOSsafari
                    "coverpic" : this.list[k].anchor_live_img,
                    "width" :  '100%',//
                    "height" : '100%',//
                    "wording": {
                        2032: "",
                        2048: "m3u8",
                        1002: '',
                        5: ' MSE  Flash '
                    },
                    'live': true,
                    'controls': 'none',
                    'listener': function(msg){
                       if(msg.type == "ended"){
                            $('#id_test_video .vcp-error-tips').text('...').show()
                            setTimeout(()=>{
                                that.play()
                            },2000) 
                        }
                }
                }); 
            }
        })

      },
    go (k) {
        var id = this.list[k].user_id
        if(id == this.$store.state.user_id){
            // this.$router.push({path:'/anchorLive'})
            window.open('/anchorLive')
        }else{
            // this.$router.push({path:'/liveRoom',query:{'id': id}})
            window.open('/liveRoom?id='+id)
        }
    },
    play () {
        let that = this
        this.player.destroy()//
        var k = this.type
        if(this.list[k].anchor_live_log_playback_url){
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[k].anchor_live_log_playback_url, //
                "flv": this.list[k].anchor_live_log_playback_url,
                "autoplay" : true,      //iOSsafari
                "coverpic" : this.list[k].anchor_live_img,
                "width" :  '100%',//
                "height" : '100%',//
                "wording": {
                    2032: "",
                    2048: "m3u8",
                    1002: '',
                    5: ' MSE  Flash '
                },
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('...').show()
                         setTimeout(()=>{
                             that.play()
                         },2000) 
                    }
                }
                // 'live': true,
                // 'controls': 'none'
            }); 
        }else{
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[k].anchor_live_play_m3u8, //
                "flv": this.list[k].anchor_live_play_flv,
                "autoplay" : true,      //iOSsafari
                "coverpic" : this.list[k].anchor_live_img,
                "width" :  '100%',//
                "height" : '100%',//
                "wording": {
                    2032: "",
                    2048: "m3u8",
                    1002: '',
                    5: ' MSE  Flash '
                },
                'live': true,
                'controls': 'none',
                'listener': function(msg){
                     if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('...').show()
                         setTimeout(()=>{
                             that.play()
                         },2000) 
                    }
                }
            }); 
        }
    }
  },
  watch: {
      list () {
        let that = this
        for(var i=0;i<this.list.length;i++){
            if(this.list[i].anchor_live_img == ''){
                this.list[i].anchor_live_img = '/static/images/lebobg.png'
            }
        }
        this.type = 0
        if(this.list[0].anchor_live_log_playback_url){
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[0].anchor_live_log_playback_url, //
                "flv": this.list[0].anchor_live_log_playback_url,
                "autoplay" : true,      //iOSsafari
                "coverpic" : this.list[0].anchor_live_img,
                "width" :  '100%',//
                "height" : '100%',//
                "wording": {
                    1002: '',
                    2032: "",
                    2048: "m3u8",
                    1002: '',
                    5: ' MSE  Flash '
                },
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('...').show()
                         setTimeout(()=>{
                             this.player.load();//
                         },2000) 
                    }
                }
                // 'live': true,
                // 'controls': 'none'
            }); 
        }else{
            this.player = new TcPlayer('id_test_video', {
                "m3u8": this.list[0].anchor_live_play_m3u8, //
                "flv": this.list[0].anchor_live_play_flv,
                "autoplay" : true,      //iOSsafari
                "coverpic" : this.list[0].anchor_live_img,
                "width" :  '100%',//
                "height" : '100%',//
                "wording": {
                    1002: '',
                    2032: "",
                    2048: "m3u8",
                    1002: '',
                    5: ' MSE  Flash '
                },
                'live': true,
                'controls': 'none',
                'listener': function(msg){
                    if(msg.type == "ended"){
                        $('#id_test_video .vcp-error-tips').text('...').show()
                         setTimeout(()=>{
                             that.play()
                            // that.player.load();//
                         },2000) 
                    }
                }
            }); 
        }
        // this.imgs = this.list[0].anchor_live_img
      }
  },
  created () {
        var that = this
        //
        var hiddenProperty = 'hidden' in document ? 'hidden' :    
            'webkitHidden' in document ? 'webkitHidden' :    
            'mozHidden' in document ? 'mozHidden' :    
            null;
        var visibilityChangeEvent = hiddenProperty.replace(/hidden/i, 'visibilitychange');
        var onVisibilityChange = ()=>{
            if (!document[hiddenProperty]) {    
                // document.title='(**)';
                // that.player.play()//
                var k = this.type
                if(this.list[k].anchor_live_log_playback_url){
                    this.player = new TcPlayer('id_test_video', {
                        "m3u8": this.list[k].anchor_live_log_playback_url, //
                        "flv": this.list[k].anchor_live_log_playback_url,
                        "autoplay" : true,      //iOSsafari
                        "coverpic" : this.list[k].anchor_live_img,
                        "width" :  '100%',//
                        "height" : '100%',//
                        "wording": {
                            2032: "",
                            2048: "m3u8",
                            1002: '',
                            5: ' MSE  Flash '
                        },
                        'listener': function(msg){
                            if(msg.type == "ended"){
                                $('#id_test_video .vcp-error-tips').text('...').show()
                                setTimeout(()=>{
                                    that.play()
                                },2000) 
                            }
                        }
                        // 'live': true,
                        // 'controls': 'none'
                    }); 
                }else{
                    this.player = new TcPlayer('id_test_video', {
                        "m3u8": this.list[k].anchor_live_play_m3u8, //
                        "flv": this.list[k].anchor_live_play_flv,
                        "autoplay" : true,      //iOSsafari
                        "coverpic" : this.list[k].anchor_live_img,
                        "width" :  '100%',//
                        "height" : '100%',//
                        "wording": {
                            2032: "",
                            2048: "m3u8",
                            1002: '',
                            5: ' MSE  Flash '
                        },
                        'live': true,
                        'controls': 'none',
                        'listener': function(msg){
                            if(msg.type == "ended"){
                                $('#id_test_video .vcp-error-tips').text('...').show()
                                setTimeout(()=>{
                                    that.play()
                                },2000) 
                            }
                        }
                    }); 
                }
            }else{
                // document.title='()  ';
                // that.player.pause()//
                that.player.destroy()//
                
            }
        }
        document.addEventListener(visibilityChangeEvent, onVisibilityChange);
  }
} 



